import React, { Component } from 'react';
import { Select, Input, Button, message, Upload } from 'antd';
import { reqAddQuestions, reqGetPublic } from '../../api';
import {
  questionType,
  gradeType,
  courseType,
  complexityType,
  yearType,
  topDataType,
} from './Variable';
import {
  reqGetYears,
  reqGetLabel,
  reqGetGrade,
  reqGetCourse,
  reqGetKnowledges,
  reqGetAllQuestions,
} from '../../api';
import memoryUtils from '../../utils/memoryUtils';
const { TextArea } = Input;

const { Option } = Select;

class AddPaper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      questionsStart: '',
      questionsAnalyze: '',
      questionsAnswer: '',
      questionsRemark: '',
      selectPaper: {},
      //   题干图片：
      questionsStartImages: null,
      // 答案对应图片：
      questionsAnswerImages: null,
      // 分析对应图片：
      questionsAnalyzeImages: null,

      // 点评对应图片：
      questionsRemarkImages: null,
      // 解答对应图片：
      questionsExplainImages: null,

      topData: {
        questionsType: ['单选题', '多选题', '判断题', '填空题', '解答题'],
        complexity: ['容易', '较易', '一般', '难', '较难', '困难'],
      },
    };
    this.payload = {
      complexity: '-1',
      user_id: '',
      course: null,
      grade: null,
      knowledge: null,
      label: null,
    };
  }

  async componentDidMount() {
    let results = await reqGetAllQuestions();
    if (results.data) {
      this.setState({
        topData: {
          ...results.data,
          questionsType: ['单选题', '多选题', '判断题', '填空题', '解答题'],
          complexity: ['容易', '较易', '一般', '难', '较难', '困难'],
        },
      });
    }
  }

  handleChange = (key, value) => {
    const { selectPaper } = this.state;
    this.setState({ selectPaper: { ...selectPaper, [key]: value } });
  };
  handleChangeInput = (key, value) => {
    this.setState({ [key]: value });
  };

  handleFileChange = (key) => (files) => {
    this.setState({ [key]: files.fileList.filter((file) => file.size <= 10 * 1024 * 1024) });
  };

  beforeUpload = (file) => {
    if (file.size > 10 * 1024 * 1024) {
      message.error('图片大小不能超过10M');
    }
    return false;
  };

  handleAddPaper = async () => {
    const user = memoryUtils.user;
    const {
      selectPaper,
      questionsStart,
      questionsAnalyze,
      questionsAnswer,
      questionsRemark,
      questionsExplain,
      //   题干图片：
      questionsStartImages,
      // 答案对应图片：
      questionsAnswerImages,
      // 分析对应图片：
      questionsAnalyzeImages,

      // 点评对应图片：
      questionsRemarkImages,
      // 解答对应图片：
      questionsExplainImages,
    } = this.state;
    const payload = {
      ...this.payload,
      ...selectPaper,
      questionsStart,
      questionsAnalyze,
      questionsAnswer,
      questionsRemark,
      questionsExplain,
      //   题干图片：
      questionsStartImages,
      // 答案对应图片：
      questionsAnswerImages,
      // 分析对应图片：
      questionsAnalyzeImages,
      // 点评对应图片：
      questionsRemarkImages,
      // 解答对应图片：
      questionsExplainImages,
      userId: user.id || 1,
    };
    if (!payload.grade) {
      payload.grade = payload.grad || '初三';
    }

    let body = new FormData();
    Object.keys(payload).forEach((key) => {
      if (key.includes('Images')) {
        let file = (payload[key] || []).map((f) => f.originFileObj);
        file.forEach((f) => {
          body.append(key, f);
        });
      } else {
        body.append(key, payload[key] || '');
      }
    });

    console.log('add paper body', body);

    let result = await reqAddQuestions(body);
    if (result.status === 200) {
      message.success(result.message);
    } else {
      message.error(result.message);
    }
  };

  renderPaperInput = (className, key, title) => {
    return (
      <div className={className}>
        <span className="paper-span">{title}</span>
        <TextArea className="input" onChange={(e) => this.handleChangeInput(key, e.target.value)} />
        <Upload
          beforeUpload={this.beforeUpload}
          onChange={this.handleFileChange(`${key}Images`)}
          accept=".png, .jpg, .jpeg"
          fileList={this.state[`${key}Images`]}
        >
          <Button>选择文件</Button>
        </Upload>
      </div>
    );
  };

  render() {
    const { topData, selectPaper } = this.state;

    return (
      <div className="paper">
        <div style={{ display: 'flex', flexWrap: 'wrap' }}>
          {Object.keys(topDataType).map((t, index) => {
            return (
              <div className="paper-card" key={index}>
                <span className="paper-span">{t}</span>
                <Select
                  value={selectPaper[topDataType[t]]}
                  style={{ width: 120 }}
                  onChange={(value) => this.handleChange(topDataType[t], value)}
                >
                  {(topData[topDataType[t]] || []).map((s, index) => {
                    let newValue = s;
                    if (t === '难易程度' || t === '题型') {
                      newValue = index + 1;
                    }
                    return (
                      <Option value={newValue} key={index + 1}>
                        {s}
                      </Option>
                    );
                  })}
                </Select>
              </div>
            );
          })}
        </div>
        <div style={{ display: 'flex' }}>
          <div style={{ width: '50%' }}>
            {this.renderPaperInput('paper-Input', 'questionsStart', '题干')}
            {this.renderPaperInput('paper-Input', 'questionsAnalyze', '分析')}
          </div>

          <div style={{ width: '50%' }}>
            {this.renderPaperInput('paper-Input', 'questionsAnswer', '答案')}
            {this.renderPaperInput('paper-Input', 'questionsRemark', '点评')}
          </div>
        </div>
        {this.renderPaperInput('questionsExplain', 'questionsExplain', '解答')}

        <Button className="foot-btn" onClick={this.handleAddPaper}>
          添加
        </Button>
      </div>
    );
  }
}

export default AddPaper;
