import React, { Component } from 'react';
import { Button, message } from 'antd';
import { reqGetQuestionsAnswer, reqAddQuestionsToMe } from '../../api';
import memoryUtils from '../../utils/memoryUtils';

const resultType = {
  分析: 'questionsAnalyze',
  答案: 'questionsAnswer',
  解答: 'questionsExplain',
  点评: 'questionsRemark',
};

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectPaper: {},
      showData: {},
    };
  }

  handleClick = async (key, re) => {
    const { showData } = this.state;
    const { showId, onChange } = this.props;
    const user = memoryUtils.user;
    const payload = {
      questionsId: re.id || 1,
      userId: user.id,
      ...re,
    };
    if (key === 'show') {
      if (showData[re.id]) {
        const newShowData = { ...showData };
        delete newShowData[re.id];
        this.setState({ showData: newShowData });
      } else {
        const result = await reqGetQuestionsAnswer(payload);
        if (result.data) {
          const obj = {
            [payload.questionsId]: result.data,
          };
          this.setState({ showData: { ...showData, ...obj } });
        }
      }
    }
    if (key === 'add') {
      if (showId === 'planPaper') {
        message.success('添加成功');
        onChange(showId, re, 'add');
      }
      if (showId === 'showPaper') {
        // 查看试题，添加到个人中心
        const result = await reqAddQuestionsToMe(payload);
      }
    }
  };

  render() {
    const { result, showId } = this.props;
    const { showData } = this.state;

    console.log('Paper render, result', result);
    //todo 需要看result里面的images，判断需要显示的内容
    // 在外面统一处理对应的images
    return (
      <div key={`${new Date().getTime()}`}>
        {(result || []).map((re, index) => {
          return (
            <div key={index} className="content-main">
              <span style={{ padding: 10 }}>{re.questionsStart}</span>
              {re.questionsStartImage &&
                re.questionsStartImage
                  .split(',')
                  .map((url) => <img src={`/download?filename=${url}`} key={url}></img>)}
              <div>
                {showId === 'createPlanPaper' &&
                  Object.keys(resultType).map((t) => {
                    return (
                      <div key={t} style={{ marginTop: 10 }}>
                        <span>{`【${t}】`}</span>
                        <p style={{ paddingLeft: 10 }}>{re[resultType[t]] || ''}</p>
                      </div>
                    );
                  })}
                {Object.keys(showData).length > 0 &&
                  showData[re.questionsId || re.id] &&
                  Object.keys(resultType).map((t) => {
                    return (
                      <div key={t} style={{ marginTop: 10 }}>
                        <span>{`【${t}】`}</span>
                        <p style={{ paddingLeft: 10 }}>
                          {showData[re.questionsId || re.id][resultType[t]] || ''}
                        </p>

                        {re[`${resultType[t]}Image`] &&
                          (re[`${resultType[t]}Image`] || '')
                            .split(',')
                            .map((url) => <img src={`/download?filename=${url}`} key={url}></img>)}
                      </div>
                    );
                  })}
              </div>
              <div className="foot" style={{ textAlign: 'center' }}>
                {showId !== 'createPlanPaper' && (
                  <Button
                    className="showButton"
                    onClick={() => this.handleClick('show', re, index)}
                  >
                    {showData[re.questionsId || re.id] ? '收起答案' : '查看答案'}
                  </Button>
                )}

                {showId === 'planPaper' && (
                  <span className="addButton" onClick={() => this.handleClick('add', re, index)}>
                    +添加到教案
                  </span>
                )}
                {showId === 'showPaper' && (
                  <span className="addButton" onClick={() => this.handleClick('add', re, index)}>
                    +添加到我
                  </span>
                )}
              </div>
            </div>
          );
        })}
      </div>
    );
  }
}

export default Main;
